<template>
<ts-dialog v-model="dialog.show" width="80%" title="">
  <div class="dialog-confirmSend">
    <ts-row>
      <ts-col :span="12">
        <ts-row class="dialog-confirmSend--flex">
          <ts-col :span="5">
            <div class="dialog-confirmSend--cover">
              <span class="kinds">{{getItemsLength}}</span>
              <ts-image width="80" height="80" :src="data.orderItems[0].picUrl" :canView="false"
           disabledHover></ts-image>
            </div>
          </ts-col>
          <ts-col :span="19">
            <p>{{data.orderMoney}}</p>
            <p>
             {{data.deliverType | filterDict(DICT.FreightSetting)}}
              <span v-show="data.deliverType===4">
                ({{data.logisticsMoney}})
              </span>
            </p>
          </ts-col>
        </ts-row>
        <ts-row>
          <ts-col :span="6"></ts-col>
          <ts-col :span="18">{{data.address.province}}{{data.address.city}}{{data.address.county}}{{data.address.address}}350200{{data.address.name}}{{data.address.phone}}</ts-col>
          <!-- TODO:  getCompanyInfo -->
          <!-- <ts-col :span="6"></ts-col> -->
          <!-- <ts-col :span="18">8 35020013928766967</ts-col> -->
        </ts-row>
      </ts-col>
      <ts-col :span="12">
        <ts-table :data="data.orderItems">
          <ts-column slot data-key="productId"  align="center" name=""></ts-column>
          <ts-column slot data-key="price"  align="center" name=""></ts-column>
          <ts-column slot data-key="num" align="center" name=""></ts-column>
        </ts-table>
      </ts-col>
    </ts-row>
    <div v-if="false">
      <ts-input></ts-input>
      <p v-show="data.deliverType===5"></p>
      <p v-show="data.deliverType===2"></p>
    </div>
  </div>
</ts-dialog>
</template>
<script>
import DICT from '@/common/dict/index';
export default {
  data() {
    return {
      DICT: {
        FreightSetting: DICT.FreightSetting
      }
    };
  },
  computed: {
    getItemsLength() {
      if (this.data.orderItems) {
        return this.data.orderItems.length;
      }
    }
  },
  props: {
    data: {
      type: Object
    },
    dialog: {
      type: Object,
      default: function(argument) {
        return {
          show: false
        };
      }
    }
  }
};
</script>
<style scoped>
  @component-namespace dialog {
    @component confirmRetainage {

    }
  }
</style>



