<template>
<!--  4. -->
<ts-dialog width="80%" v-model="dialog.show">
  <p slot="title">
    <strong>
      &nbsp;
    </strong>
    <span>{{data.orderId}}</span>&nbsp;
    <span>{{data.placeTime | filterDate('dateTime')}}</span>
  </p>
  <div class="dialog-editPrice">
    <ts-form :model="editPriceForm" :rules="rules" ref="editPriceForm" label-width="120px">
      <ts-row>
        <ts-col :span="12">
            <ts-form-item label="" prop="payType">
                <ts-switch v-model="editPriceForm.payType" :onValue="2" :offValue="0"></ts-switch>
            </ts-form-item>
            <ts-form-item label="" prop="paymentPeriod" v-if="editPriceForm.payType===0">
                <ts-input-num v-model="editPriceForm.paymentPeriod" :min="1" :max="getPaymentPeriod"></ts-input-num>&nbsp;
            </ts-form-item>
            <ts-form-item label="" prop="prepaidMoney" v-if="editPriceForm.payType===2">
                <ts-input v-model.number="editPriceForm.prepaidMoney" width="60%">
                    <span slot="append"></span>
                </ts-input>
            </ts-form-item>
            <ts-form-item label="" prop="paymentPeriod" v-if="editPriceForm.payType===2">
                <ts-input-num v-model="editPriceForm.paymentPeriod" :min="1" :max="getPaymentPeriod"></ts-input-num>&nbsp;
            </ts-form-item>
            <ts-form-item label="" prop="deliverType">
              <ts-radio-group v-model="editPriceForm.deliverType" bordered>
                <ts-radio :label="item.dicValue" :key="item.dicValue" v-for="item in DICT.FreightSetting" :title="item.tip">{{item.label}}</ts-radio>
              </ts-radio-group>
            </ts-form-item>
            <ts-form-item label="" prop="logisticsMoney" v-if="editPriceForm.deliverType===4">
                <ts-input v-model.number="editPriceForm.logisticsMoney" width="60%">
                    <span slot="append"></span>
                </ts-input>
            </ts-form-item>
            <ts-form-item label="" prop="orderMoney">
                <ts-input v-model="editPriceForm.orderMoney" width="60%">
                    <span slot="append"></span>
                </ts-input>
                <ts-button type="text"></ts-button>
            </ts-form-item>
        </ts-col>
        <ts-col :span="12" class="dialog-editPrice-detail">
          <ts-row>
            <ts-col :span="6"></ts-col>
            <ts-col :span="18">
            <span v-if="noShowMoney"></span>
            <span v-else>{{totalPay}}</span>
            </ts-col>
            <ts-col :span="6"></ts-col>
            <ts-col :span="18">
              <span v-if="editPriceForm.deliverType===4">{{editPriceForm.logisticsMoney}}</span>
              <span v-else>{{editPriceForm.deliverType | filterDict(DICT.FreightSetting)}}</span>
            </ts-col>
          </ts-row>
          <ts-row v-show="editPriceForm.payType===0">
            <ts-col :span="6"></ts-col>
            <ts-col :span="18">
              <span v-if="noShowMoney"></span>
              <span v-else>{{totalPay}}</span>
            </ts-col>
          </ts-row>
          <ts-row v-show="editPriceForm.payType===2">
            <ts-col :span="6"></ts-col>
            <ts-col :span="18">{{editPriceForm.prepaidMoney}}</ts-col>
            <ts-col :span="6"></ts-col>
            <ts-col :span="18">
              <span v-if="noShowMoney"></span>
              <span v-else>{{totalPay - editPriceForm.prepaidMoney}}</span>
            </ts-col>
          </ts-row>
          <ts-button type="primary" class="dialog-editPrice-detail--button"></ts-button>
          <p class="dialog-editPrice-detail--tip"></p>
        </ts-col>
      </ts-row>
      <ts-menu-table :header="false" class="dialog-editPrice-menu">
        <ts-menu-table-item></ts-menu-table-item>
        <ts-menu-table-item></ts-menu-table-item>
        <ts-menu-table-item></ts-menu-table-item>
      </ts-menu-table>
      <div class="dialog-editPrice-menu-table">
        <ts-menu :prop="list">
        <ts-menu-table :header="false" content v-for="(item,index) in editPriceForm.orderItems">
          <ts-menu-table-item>
            <div class="dialog-editPrice-detail--wrapper">
              <ts-image width="80" height="80" :src="item.picUrl"></ts-image>
              <div class="dialog-editPrice-detail--flex">
                <p>{{item.productId}}</p>
                <div class="detail">
                  <p><i>{{item.totalPrice}}</i>1233</p>
                  <p><i>{{item.num}}</i>1233</p>
                </div>
              </div>
            </div>
          </ts-menu-table-item>
          <ts-menu-table-item>
           <ts-form-item :prop="'orderItems.' + index + '.price'" labelWidth="0">
              <ts-input v-model="item.price" width="60%">
              </ts-input>
              &nbsp; / 
          </ts-form-item>
          </ts-menu-table-item>
          <ts-menu-table-item>
            <ts-form-item labelWidth="0">
              <ts-input-num v-model="item.num" :min="getSaleMin" :max="getSaleMax">
              </ts-input-num>&nbsp;
            </ts-form-item>
          </ts-menu-table-item>
        </ts-menu-table>
      </ts-menu>
      </div>
    </ts-form>
  </div>
  <span slot="footer"></span>
</ts-dialog>
</template>
<script>
import DICT from '@/common/dict/index';
export default {
  data() {
      // 
      var validatePaymentPeriod = (rule, value, callback) => {
        if (value > this.OrderDefaultParam.paymentPeriod) {
          callback(new Error(`${this.OrderDefaultParam.paymentPeriod}, 1`));
          return;
        }
        callback();
      };
      // 
      var validateLogisticsMoney = async(rule, value, callback) => {
        if (!value && this.editPriceForm.deliverType === 4) {
          callback(new Error('!'));
          return;
        }
        if (value && !/^[0-9]+(.[0-9]{2})?$/.test(value)) {
          callback(new Error('!'));
          return;
        }
        callback();
      };
    return {
      DICT: {
        FreightSetting: DICT.FreightSetting
      },
      num: '',
        // --
      editPriceForm: {
        deliverType: '',
        orderItems: [{
          num: '',
          price: ''
        }],
        logisticsMoney: '',
        orderMoney: '',
        payType: '',
        paymentPeriod: '',
        prepaidMoney: ''
      },
      rules: {
        logisticsMoney: {
          trigger: 'blur',
          validator: validateLogisticsMoney
        },
        orderMoney: {
          trigger: 'blur',
          pattern: /^[0-9]+(.[0-9]{2})?$/,
          message: ''
        },
        paymentPeriod: [{
          validator: validatePaymentPeriod
        }]
      }
    };
  },
  computed: {
    // 
    getSaleMax() {
      if (this.data.orderType === 1) {
        return parseInt(this.OrderDefaultParam.bulkSaleMax);
      }
      if (this.data.orderType === 3) {
        return parseInt(this.OrderDefaultParam.templateSaleMax);
      }
    },
    // 
    getSaleMin() {
      if (this.data.orderType === 1) {
        return parseInt(this.OrderDefaultParam.bulkSaleMin);
      }
      if (this.data.orderType === 3) {
        return parseInt(this.OrderDefaultParam.templateSaleMin);
      }
    },
    // 
    getPaymentPeriod() {
      return parseInt(this.OrderDefaultParam.paymentPeriod);
    },
    // 
    totalPay() {
      let total = 0;
      for (let i = 0; i < this.editPriceForm.orderItems.length; i++) {
        total = total + Number(this.editPriceForm.logisticsMoney) + this.editPriceForm.orderItems[i].price * this.editPriceForm.orderItems[i].num;
      }
      return total;
    },
    //   
    noShowMoney() {
      return this.editPriceForm.orderItems.some(item => !item.price);
    }
  },
  watch: {
    data(val) {
      console.log(val);
    }
  },
  props: {
    OrderDefaultParam: {
      type: Object
    },
    data: {
      type: Object
    },
    dialog: {
      type: Object,
      default: function() {
        return {
          show: false
        };
      }
    }
  }
};
</script>
<style scoped>
   @component-namespace dialog {
    @component editPrice{
      box-sizing: border-box;
      .ts-col-12{
        &:nth-of-type(1) {
          border-right:1px solid #eaeaea; 
        }
      }
      @descendent menu {
        margin-top: 10px;
      }
      @descendent menu-table {
        max-height: 200px;
        overflow: auto;
      }
      @descendent detail {
        padding-left: 90px;
        .ts-col {
          margin-bottom: 10px;
        }
        @modifier button {
          margin:  10px auto 10px;
        }
        @modifier wrapper{
          display: flex;
          align-items: flex-start;
        }
        @modifier flex {
          padding-left: 10px;
          position: relative;
          display: flex;
          align-items: center;
          flex-direction: column;
          .detail {
            padding-top: 10px;
            font-size: 12px;
            i {
              color : #666;
            }
          }
        }
        @modifier tip {
          font-size: 12px;
          color: #666;
        }
      }
    }
  }
</style>



